.container{
   width: 100%;
  height: 4rem;
  display: flex;
background-color: #313c40a6; 
  justify-content: space-between;
  font-family: 'Iceland', cursive;
font-family: 'Teko', sans-serif;
  font-size: 2rem;
  font-size: large;
  align-items: center;
  position: fixed;
  top: 0%;
  left: 0%;
  z-index: 100;
  backdrop-filter: blur(4px);
  
}
.middle{
    display: flex;
}
.cf{
    font-size: 23px;
    font-weight: 100;
}
.compare{
    float: inline-end;
    font-size: 23px;
    margin: 16px;
    
}
.logo{
      width: 64px;
    display: flex;
    padding: 8px 12px;
    margin-top: 9px;
}

a{
    color: white;
}